.card {
  border-radius: 5px;
  padding: 30px;
}
.card--user1 {
  background-color: var(--moderate-violet);
}
.card--user2 {
  background-color: var(--very-dark-grayish-blue);
}
.card--user3 {
  background-color: var(--white);
}
.card--user4 {
  background-color: var(--very-dark-blackish-blue);
}
.card--user5 {
  background-color: var(--white);
}
.card__icon {
  border-radius: 50%;
}
.card__username {
  color: var(--white);
  font-weight: 600;
  letter-spacing: 0.01875rem;
}
.card__username--color {
  color: var(--very-dark-grayish-blue);
}
.card__username--opac {
  opacity: 50%;
}
.card__header {
  font-size: 1.25rem;
  margin: 1.25rem 0;
  font-weight: 600;
  line-height: 1.2;
  color: var(--white);
  letter-spacing: 0.025rem;
}
.card__header--color {
  color: var(--very-dark-grayish-blue);
}
.card__body {
  line-height: 1.5;
  letter-spacing: 0.01875rem;
  color: var(--white);
}
.card__body--color {
  color: var(--very-dark-grayish-blue);
}
.card__body--opac {
  opacity: 70%;
}

.flex {
  display: flex;
  gap: 0.9375rem;
}
.flex__jc {
  justify-content: start;
}
.flex__ai {
  align-items: center;
}